.App-header {
  /* Daha canlı bir geçiş efekti için gradient kullandım */
  background: linear-gradient(45deg, #7c4dff, #6545f5);
  padding: 0.5rem 0;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.12);
  height: 50px;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

/* Header içi container */
.header-container {
  max-width: 1280px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  height: 100%;
}

/* Logo ve Timer Bölümü */
.logo {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.logo h1 {
  color: #ffffff;
  font-size: 1.3rem;
  font-weight: 700;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

/* Zaman göstergesi */
.timer {
  color: #e0e0e0;
  font-size: 0.85rem;
  padding: 0.25rem 0.5rem;
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 0.25rem;
  box-shadow: 0 0 4px rgba(255, 255, 255, 0.2);
}

/* Menü Kısmı */
.nav-menu ul {
  display: flex;
  gap: 1.5rem;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
}

/* Linklerin Stili */
.nav-link {
  color: #f2f2f2;
  text-decoration: none;
  font-size: 0.9rem;
  transition: color 0.2s ease;
  padding: 0.5rem 0;
}

/* Link üzerine gelme efekti */
.nav-link:hover {
  color: #ffffff;
  text-shadow: 0 0 3px rgba(255, 255, 255, 0.3);
}

/* Admin Link için özel stil */
.admin-link {
  background-color: #ff6f6f;
  padding: 0.5rem 1rem;
  border-radius: 0.375rem;
  color: white;
  font-weight: 500;
  transition: background-color 0.2s ease, transform 0.2s ease;
}

/* Admin link hover/tıklama */
.admin-link:hover {
  background-color: #f55353;
  transform: scale(1.05);
}

/* Giriş - Çıkış (Auth) Butonu */
.auth-button {
  background-color: transparent;
  border: 1px solid #fff;
  color: #fff;
  padding: 0.45rem 1rem;
  border-radius: 0.375rem;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s ease;
}

.auth-button:hover {
  background-color: #fff;
  color: #333;
}

/* Çıkış (Logout) Butonu: Kırmızı ton */
.auth-button.logout {
  background-color: #fd5252;
  border-color: #fd5252;
}

.auth-button.logout:hover {
  background-color: #e02c2c;
  border-color: #e02c2c;
  color: #ffffff;
}

/* Mobil uyum */
@media (max-width: 768px) {
  .header-container {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }

  .nav-menu ul {
    flex-direction: column;
    gap: 0.75rem;
  }

  .nav-link {
    display: block;
    padding: 0.5rem;
  }

  .admin-link {
    width: 100%;
    text-align: center;
  }

  .auth-button {
    width: 100%;
  }
}
