.pre-quiz-assessment {
  max-width: 600px;
  margin: 2rem auto;
  padding: 2rem;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.pre-quiz-assessment h2 {
  color: #1a1a1a;
  margin-bottom: 2rem;
  text-align: center;
  font-size: 1.8rem;
}

.level-selection {
  margin-bottom: 2rem;
}

.level-selection h3 {
  color: #4b5563;
  margin-bottom: 1.5rem;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 500;
}

.level-buttons {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.level-buttons button {
  padding: 1.5rem;
  border: 2px solid #e5e7eb;
  border-radius: 12px;
  background: white;
  color: #4b5563;
  cursor: pointer;
  transition: all 0.2s;
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.level-buttons button:hover {
  border-color: #3b82f6;
  background: #f8fafc;
}

.level-buttons button.active {
  background: #3b82f6;
  border-color: #3b82f6;
  color: white;
}

.level-description {
  font-size: 0.9rem;
  opacity: 0.8;
}

.level-buttons button.active .level-description {
  color: #fff;
}

.submit-button {
  display: block;
  width: 100%;
  margin-top: 2rem;
  padding: 1rem;
  background: #3b82f6;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s;
}

.submit-button:hover {
  background: #2563eb;
}
